@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Karla', sans-serif;
  color: #dbdbdc;
  background-color: #1c1c27;
}

.game-wrapper {
  font-weight: bold;
  width: 80%;
  margin: 50px auto;
  background: #28293d;
  padding: 20px;
  box-shadow: 0 3px 15px rgb(0 0 0 / 0.05);
  border-radius: 5px;
}
.game-details {
  text-transform: uppercase;
  border-bottom: 2px solid rgb(255 255 255 / 0.1);
}
.correct {
  color: #3be28f;
  -webkit-animation: blinker 2s linear infinite;
          animation: blinker 2s linear infinite;
}
.incorrect {
  color: #f75d5d;
  -webkit-animation: blinker 2s linear infinite;
          animation: blinker 2s linear infinite;
}
.game-inner {
  width: 55%;
  padding: 20px 0px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.country-flag {
  width: auto;
  height: auto;
}
.choice {
  cursor: pointer;
  min-width: 350px;
  font-size: 15px;
  background: #2b2c44;
  color: #6293f5;
  letter-spacing: 1px;
  margin: 15px 0;
  padding: 15px;
  box-shadow: 0 3px 15px rgb(0 0 0 / 0.02);
	transition: .425s;
}
.choice:hover {
  background: #242539;
}
@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}
@media (max-width: 1440px) {
  .game-inner {
    width: 90%;
  }
}
@media (max-width: 834px) {
  .game-inner {
    width: 100%;
  }
  .game-quiz {
    flex-direction: column;
  }
  .choice {
    min-width: auto;
  }
}
